.warsaw-map {
    width: auto;
    height: 70vh;
    float: right;
    -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, .7));
    filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, .7));

    .district {
        fill:lightsteelblue;
        stroke: darkslategray;
        stroke-width: 2px;
        transition: all 0.4s ease 0s;
        cursor: pointer;
    }

    path:hover {
        fill: lightslategray;
        transition: all 0.4s ease 0s;
    }

    .true {
        // Used to indicate if the path has been clicked.
        // Defined by parent state (state.district-name).
        fill: slategray;
        transition: all 0.4s ease 0s;
    }

    .river {
        fill: deepskyblue;
        transition: all 0.4s ease 0s;
        cursor: pointer;
    }

    .river.true {
        // Used to indicate if the path has been clicked.
        // Defined by parent state (state.district-name).
        fill: midnightblue;
        transition: all 0.4s ease 0s;
    }

    text {
        pointer-events: none;
    }
}